import { __ } from '@wordpress/i18n';
import { useState, useEffect } from 'react';
const $ = jQuery;

import { tabController } from '../../Components/utils/functions';

import { fetchUserWithData } from './services';
import Settings from './Settings';
import Style from './Style';
import GalleryHeader from './GalleryHeader';
import Gallery from './Gallery';
import { hasObjProperty } from './utils/function';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { cId, accessToken } = attributes;

	useEffect(() => { clientId && '' === cId && setAttributes({ cId: clientId }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	const [users, setUsers] = useState([]);
	const [usersData, setUsersData] = useState({});
	const [pageNumber, setPageNumber] = useState(1);

	const userName = users?.length ? users[0]?.username : '';
	const userData = userName && usersData && hasObjProperty(usersData, userName) ? usersData[userName] : {};

	// useEffect(() => {
	// 	token && fetchUser(token).then(data => {
	// 		setUsers([data]);
	// 	});
	// }, [token]);
	// useEffect(() => {
	// 	token && userName && fetchUserData(token, userName).then(data => {
	// 		setUsersData({ [userName]: data });
	// 	});
	// }, [token, userName]);

	useEffect(() => {
		let usersHolder = [];
		let usersDataHolder = {};

		accessToken?.length && accessToken.map(token => {
			fetchUserWithData(token).then(data => {
				usersHolder.push(data.user);
				usersDataHolder = { ...usersDataHolder, ...data.userData }

				setUsers(usersHolder);
				setUsersData(usersDataHolder);
			});
		});
	}, [accessToken]);

	return <>
		<Settings attributes={attributes} setAttributes={setAttributes} clientId={clientId} setPageNumber={setPageNumber} />

		<div className={className} id={`ifbInstagramFeed-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			{accessToken?.[0] ? <div className='ifbInstagramFeed'>
				{!$.isEmptyObject(usersData) ? <>
					<GalleryHeader userName={userName} attributes={attributes} />

					<Gallery media={userData.media} attributes={attributes} pageNumber={pageNumber} setPageNumber={setPageNumber} />
				</> : ''}
			</div> : <p className='ifbInsertAccess'>{__('Please Insert Access Token', 'instagram-feed')}</p>}
		</div>
	</>;
};
export default Edit;